/* common */
@import url(./reset.css);
@import url(./common.css);
@import url(./nav.css);
@import url(./header.css);
/* // common */
/* pages */
@import url(./dashboard.css);
/* // pages */

img,
svg,
video,
canvas,
audio,
iframe,
embed,
object {
  display: block;
  vertical-align: middle;
}

* {
  -webkit-tap-highlight-color: transparent;
  text-rendering: optimizeLegibility;
}

html {
  font-size: 14px;
  -webkit-font-smoothing: auto;
  -webkit-text-size-adjust: 100%;
  font-family: Inter var, 'Roboto', Helvetica Neue, Arial, sans-serif;
}

body {
  background: rgb(241, 245, 249);
  color: rgb(17, 24, 39);
  font-family: 'Inter var', 'Roboto', Helvetica, Arial, sans-serif;
  font-feature-settings: 'salt';
  font-size: 14px;
  line-height: 1.4;
  -webkit-font-smoothing: auto;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 7px;
  background-color: white;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb {
  background-color: #b9b9b9;
  background-clip: padding-box;
  border: 2px solid transparent;
  border-radius: 10px;
}

/* main layouts */
main {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  min-height: 100%;
  z-index: 10;
  position: relative;
  min-width: 0;
}

#layout {
  flex: 1 1 auto;
  display: flex;
  min-width: 0;
}

main > .container {
  max-width: 1570px;
  width: 100%;
  margin: 0px auto;
  box-sizing: border-box;
}

.section__grid__wrap {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 24px;
}

.container > div {
  padding: 24px 36px 24px;
  box-sizing: border-box;
}

#pageTitleWrap {
  grid-column: span 4 / span 4;
  box-sizing: border-box;
  padding: 24px 36px 0px;
}

#pageTitleWrap > h2 {
  font-size: 1.8rem;
  margin-bottom: 4px;
}

#pageTitleWrap > p {
  color: rgb(135, 140, 150);
}

#pageTitleWrap > ul {
  display: flex;
}

#pageTitleWrap > ul li {
  display: flex;
  align-items: center;
}

#pageTitleWrap > ul > li span {
  color: rgb(135, 140, 150);
  margin: 0 6px;
}

#pageTitleWrap > ul > li p,
#pageTitleWrap > ul > li a {
  text-decoration: none;
  color: rgb(135, 140, 150);
  cursor: pointer;
}

#pageTitleWrap > ul > li p:hover,
#pageTitleWrap > ul > li a:hover {
  color: var(--main-color);
}

#pageTitleWrap > ul > li p,
#pageTitleWrap > ul > li a {
  pointer-events: none;
}

#pageTitleWrap > ul > li:last-of-type span {
  display: none;
}

.section__grid__wrap > section > .section__inner {
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000),
    var(--tw-shadow);
  --tw-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --tw-shadow-colored: 0 1px 3px 0 var(--tw-shadow-color), 0 1px 2px 0 var(--tw-shadow-color);
  border-radius: 16px;
  background-color: rgb(255, 255, 255);
  color: rgb(17, 24, 39);
  transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.section__grid__wrap > section > .section__inner > .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.section__grid__wrap > section > .section__inner > .top.line {
  padding-bottom: 16px;
  box-sizing: border-box;
  border-bottom: 1px solid #e6e6e6;
}

.section__grid__wrap > .section__title {
  font-size: 1.3rem;
}

.section__grid__wrap > section > .section__inner > .top > .normal__title {
  font-size: 1.1rem;
  color: rgb(107, 114, 128);
}

.section__grid__wrap > section > .section__inner {
  box-sizing: border-box;
  padding: 24px;
}

.content > .sub__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content .sub__nav .left {
  display: flex;
  align-items: flex-start;
}

.content .sub__nav .left button {
  margin-right: 12px;
  width: 33px;
  height: 33px;
  background: rgb(187, 187, 187);
  border: none;
  cursor: pointer;
  border-radius: 8px;
  -webkit-transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.content .sub__nav .left button svg {
  fill: #fff;
}

.content .sub__nav .left button:hover {
  background: var(--main-color);
}

.content .sub__nav .left dt {
  font-size: 1.5rem;
}

.content .sub__nav .left dd {
  color: rgb(107, 114, 128);
}

/* // main layouts */

.f__bold {
  font-weight: 800;
}

.f__medium {
  font-weight: 600;
}

.modal__bg {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99998;
  display: none;
}

.gridjs-wrapper {
  min-height: 493.6px;
  overflow-x: auto;
}

table.gridjs-table {
  width: 100%;
  min-width: 1100px;
}

table.gridjs-table tr:last-of-type {
  border-bottom: 1px solid #e5e7eb;
}

table.gridjs-table tr td {
  white-space: nowrap;
  overflow: hidden;
  background: none;
}

.gridjs-container {
  overflow: auto;
}

table.gridjs-table tr td {
  text-overflow: ellipsis;
}

.react-datepicker__input-container > input {
  font-size: 1.1rem;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px #e9e9e9;
  border: 1px solid #aeaeae;
  line-height: 16px;
  padding: 12px 10px;
  max-width: 150px;
  pointer-events: none;
  box-sizing: border-box;
}

.react-datepicker__input-container::before {
  content: '';
  position: absolute;
  display: block;
  opacity: 0.1;
  width: calc(100% - 42px);
  height: 100%;
  left: 0;
  top: 0;
}

.react-datepicker__input-container:after {
  content: '';
  position: absolute;
  display: block;
  width: 42px;
  opacity: 0.7;
  height: 100%;
  right: 0;
  top: 0;
  border-radius: 30px;
  cursor: pointer;
  background: url(../img/icon/ic_calendar.svg) no-repeat 50% 50%;
}

#map .markerBox {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#map .markerBox > .title {
  background: white;
  padding: 4px 6px;
  border-radius: 8px;
  margin-bottom: 8px;
  border: 3px solid var(--main-color);
  white-space: nowrap;
  font-size: 0.8rem;
}

#map .markerBox .marker__wrap {
  max-width: 40px;
  max-height: 40px;
  display: flex;
  justify-content: center;
  box-shadow: 0 39px 67px 3px hsl(0deg 0% 67% / 30%);
}

#map .markerBox .marker__wrap > img {
  width: 100%;
  height: 100%;
}

#loadingWrap {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
}

#loadingWrap > .bg {
  background: rgba(255, 255, 255, 0.6);
  width: 100%;
  height: 100%;
}

#loadingWrap > div {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
}

#postSearchWrap {
  position: fixed;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  width: 400px;
  height: 400px;
  z-index: 100;
  display: flex;
  flex-direction: column-reverse;
}

#postSearchWrap > .close__btn {
  background-color: #636363;
  border: none;
  padding: 8px 0;
  box-sizing: border-box;
  cursor: pointer;
  color: #fff;
}

.gridjs-wrapper button.gridjs-sort {
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
}

.ui.dimmer {
  display: none;
  position: fixed;
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;
  text-align: center;
  vertical-align: middle;
  padding: 1em;
  background-color: rgba(0, 0, 0, 0.6);
  line-height: 1;
  animation-fill-mode: both;
  animation-duration: 0.5s;
  transition: background-color 0.5s linear;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
  user-select: none;
  z-index: 1000;
  perspective: 2000px;
  transform-origin: center center;
}

.ui.dimmer > div {
  background: white;
  border-radius: 8px;
  box-sizing: border-box;
  min-width: 800px;
  overflow: hidden;
  box-shadow: 0 20px 60px 0 rgba(29, 75, 155, 0.45);
}

.ui.dimmer > div .content__header {
  box-sizing: border-box;
  padding: 12px;
  border-bottom: 1px solid rgba(34, 36, 38, 0.15);
  background: #f9fafb;
}

.ui.dimmer > div .content__header h3 {
  font-size: 1.4rem;
}

.ui.dimmer > div .content__header h4 {
  margin-top: 12px;
  font-size: 1.1rem;
}

.ui.dimmer > div .content__header h3 > span {
  color: var(--main-color);
}

.ui.dimmer > div .content__body {
  max-height: 400px;
  overflow-y: auto;
}

.ui.dimmer > div .btn__wrap {
  display: flex;
  justify-content: flex-end;
  box-sizing: border-box;
  padding: 12px;
  border-top: 1px solid rgba(34, 36, 38, 0.15);
  background: #f9fafb;
}

.ui.dimmer > div .btn__wrap button:first-of-type {
  margin-right: 14px;
}

@media (max-width: 1200px) {
  .main__nav + .modal__bg {
    display: block;
  }

  .main__nav.hide + .modal__bg {
    display: none;
  }
}

@media (max-width: 960px) {
  .section__grid__wrap {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

/* max-width : 600 ?? */
@media (max-width: 660px) {
  .section__grid__wrap {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}
